@import "variables";
@import "bourbon";

footer {
    @include display(flex);
    @include align-items(center);
    height: 135px;
    background-color: $footer_bg;

    .logo-block {
        width: 521px;
        float: left;
    }
    ul {
        padding-left: 0px;
        position: absolute;
        right: 15px;

        li {
            display: inline-block;
            font-size: 16px;
            list-style-type: none;
            color: #989898;
            padding-left: 20px;

            &:first-child {
                padding-left: 0px;
            }
        }
    }
}

@media screen and ($tablet) {
    footer .logo-block {
        width: 100%;
        float: left;
        margin: 0 auto;
        text-align: center;
        padding-bottom: 10px;
    }
    footer ul {
        position: static;
        width: 100%;
        float: left;
        text-align: center;
    }
}