@import "variables";
@import "bourbon"; 


/* Text cases */
.text-ncase {
    text-transform: none; }

.text-lowercase {
    text-transform: lowercase; }

.text-uppercase {
    text-transform: uppercase; }

/* Font styles */
.text-normal {
    font-style: normal; }

.text-italic {
    font-style: italic; }

.text-strike {
    text-decoration: line-through; }

/* Text alignment */
.text-left {
    text-align: left; }

.text-center {
    text-align: center }

.text-right {
    text-align: right; }

.align-center {	
    @include align-items(center); }

/* Colors */
.text-black {
    color: #000; }

.bg-black {
    background-color: #000; }

.text-white {
    color: #fff; }

.bg-white {
    background-color: #fff; }

/** Backgrounds */
.bg-none {
    background-color: transparent; }

/* Utilities */
.clear-both {
    clear: both; }

.float-none,
.clear-float,
.no-float {
    float: none; }

.float-right{	
    float:right;
}

.overflow-hidden {
    overflow: hidden; }

.overflow-visible {
    overflow: visible; }

.overflow-auto {
    overflow: auto; }

.display-block {
    display: block; }

.display-inline {
    display: inline; }

.display-inline-block {
    display: inline-block; }

.display-flex {	
    display:flex;	}

.display-inline-flex {	
    display: inline-flex; }

html.display-table .display-table {
    display: table; }

html.display-table-row .display-table-row {
    display: table-row; }

html.display-table-cell .display-table-cell {
    display: table-cell; }

.position-absolute {
    position: absolute; }

.position-static {
    position: static; }

.position-relative {
    position: relative; }

h1,h2,h3,h4,h5,h6,p,ul,li {	
    margin: 0;
}

.hamburger-inner,.hamburger-inner:before,.hamburger-inner:after {
    background-color: #bebec0;
}
.container {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    max-width: 970px;
}

.mar-left-xl
{
    margin-left: 15px;
}

.flex {
    @include display(flex);
}

// repeated elements buttons, headers etc.
/*  .box-header-gray {
            padding:40px 0px 44px;
        }*/

.box-header {


    h2 {
        font-size: 60px;
        font-family: "Roboto-Light";
    }
    h3 {
        font-size: 40px;
        font-family: "Roboto-Regular";
    }
}

.browny-box {
    background-color: $brown_bg;
}
.box-header-gray {
    h3 {
        font-size: 36px;
    }
    .head-lines {
        width: 100%;
        margin-bottom: 44px;
        border-bottom: 1px solid #424242;

        span {
            position: relative;
            top: 10px;
            background-color: #282a2e;
            padding: 0px 35px;
        }
    }
}

.line-header-box {
    text-align: left;
    margin-bottom: 24px;
    font-size: 18px;
    font-family: "Roboto-Bold";

    &:after {
        content: '';
        width: 100%;
        height: 2px;
        background-color: #424242;
        display: block;
        position: relative;
        top: 7px;
    }
}


.dark-bg {
    background-color: $altColor;
}

.white-bg {
    background-color: #fff;
}

.hamburger {
    display: none;
}

.purchase-btn {
    width: 131px;
    height: 43px;
    border-radius: 5px;
    color: $baseColor;
    font-size: 18px;
    @include display(flex);
    @include align-items(center);
    @include justify-content(center);

    &:hover {
        color: $baseColor;
        text-decoration: none;
    }
}


html {
    font-size: $em-base;
    color: $baseColor;
}


body {
    background-color: $dark_bg;
    font-family: $baseFont;
    font-size: $em-base;
    color: $baseColor;
}

p {
    color: #989898;
}

// columns 
.contact-details {
    width: 40%;
    max-width: 226px;
}
.contact-form {
    width: 60%;
    max-width: 669px; 
}
.pricing-col-small {
    width: 25%;
    float: left;
}

.person-details {
    width: 225px;
    float: left;
    padding-bottom: 60px;

    .name-det {
        font-size: 20px;
        padding-top: 25px;
    }
    .role-det {
        font-size: 16px;
        padding-bottom: 18px;
    }
    .box-header-gray {
        padding-bottom: 52px;
    }
    p {
        color: #adadad;
        line-height: 1.6;
        padding: 0px 12px;
    }
}
.circle {
    float: left;
    width: 15.935%;
    margin-right: 4.946%;
}

.pricing-col-large {
    width: 33.333%;
    float: left;
}

.recent-tweets-box,.recent-posts-box,.photo-stream-box {
    width: 25%;
    float: left;
}

// BANNER
section.banner {
    height: 589px;
    background-color: $jumbotron_bg;
    background-image: url('../images/jumbo_icons.png');
    background-repeat: no-repeat;
    background-position: 80% 100%;
}

// OUR-SERVICES
section.our-services {
    text-align: center;
    padding-bottom: 62px;

    .box-header-gray {
        padding: 52px 0px;
    }
    .icon-container {
        width: 101px;
        height: 101px;
        background-color: $altColor;
        border-radius: 50%;
        margin: 0 auto;
        @include display(flex);
        @include align-items(center);
        @include justify-content(center);
    }
    .services-item-container {
        max-width: 923px;
        margin: 0 auto;

        .one-four {
            float: left;
            min-height: 271px;

            h4 {
                padding: 19px 0px 15px;
                font-size: 24px;
            }
            p {
                color: #b3b3b3;
                padding: 0px 33px 0px;
            }
        }
    }
}

// PORTFOLIO
section.portfolio {
    .browny-box {
        padding: 144px 0px 144px;

        .box-header {

            h2 {
                padding-bottom: 26px;
            }

            .purchase-btn {
                background-color: $grassColor;
                border-bottom: 3px solid #798f21;
                cursor: pointer;
                @include display(inline-flex);

                &:last-child {
                    background-color: $aquaColor;
                    border-bottom: 3px solid #1f937d;
                    margin-left: 4px;
                }
            }
        }
    }
    .portfolio-gray-box {
        padding: 59px 0px 59px;
        text-align: center;

        .category-panel-list {
            padding: 16px 0px 39px;

            li {
                padding: 11px 0px 11px;
                list-style-type: none;
                background-color: $altColor;
                width: 104px;
                display:inline-block;
                margin: 0 auto;

                a {
                    color: $baseColor;
                    font-size: 14px;

                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
        .photos-container {
            .col-md-3 {
                padding-left: 0px;
            }
            .photo-row {
                max-width: 940px;
                margin: 0 auto;

                &:first-child {
                    //margin-bottom: 15px;
                }

                .photo-col {
                    @include display(flex);
                    @include justify-content(center);
                    float: left;                    
                    position: relative;
                    margin-bottom: 14px;

                }

                .photo-b {
                    background-color: $altColor;
                    position: relative;
                    float: left;
                    width: 100%;
                    max-width: 223px;
                    height: 223px;
                }
            }
        }
        .load-more {
            display: block;
            color: $baseColor;
            font-size: 16px;
            margin-top: 43px;
            clear: both;

            &:after {
                content: '';
                display: block;
                position: relative;
                width: 31px;
                height: 9px;
                background-image: url("../images/load_icon.png");
                background-repeat: no-repeat;
                margin: 0 auto;
                top: 11px;
            }

            &:hover {
                text-decoration: none;
            }
        }
    }   
}

// PRICING
section.pricing {

    .browny-box {
        padding: 161px 0px 167px;
    }

    .our-price {
        text-align: center;

        .box-header-gray {
            padding: 41px 0px 44px;
        }
        .pricing-row{ 
            &:first-child {              
                padding-bottom: 67px;
                width: 100%;
                max-width: 1000px;
                margin: 0 auto;
                overflow: hidden;

                .pricing-col-large {
                    &:first-child {
                        margin-left: 0px;
                    }
                }
            }

            &:last-child {
                padding-bottom: 69px;
                width: 100%;
                max-width: 970px;
                margin: 0 auto;
                overflow: hidden;

                .pricing-col-small {

                    &:first-child {
                        margin-left: 0px;

                        .price-card {
                            .cost-block {
                                background-color: #424242;
                                border-bottom: 3px solid #2d2d2d;
                            }
                            .purchase-btn {
                                background-color: #e4e4e4;
                                border-bottom: 3px solid #c1c1c1;
                                color: #424242;
                            }
                        }
                    }
                    &:nth-of-type(2) {
                        .price-card {
                            .cost-block {
                                background-color: $pastelaquaColor;
                                border-bottom: 3px solid #55857b;
                            }
                            .purchase-btn {
                                background-color: $pastelaquaColor;
                                border-bottom: 3px solid #55857b;
                            }
                        }
                    }
                    &:nth-of-type(3) {
                        .price-card {
                            .cost-block {
                                background-color: $pastelgrassColor;
                                border-bottom: 3px solid #8e9865;
                            }
                            .purchase-btn {
                                background-color: $pastelgrassColor;
                                border-bottom: 3px solid #8e9865;
                            }
                        }
                    }
                    &:nth-of-type(4) {
                        .price-card {
                            .cost-block {
                                background-color: #e4e4e4;
                                border-bottom: 3px solid #a7a7a7;
                                color: $pastelaquaColor;
                            }
                            .purchase-btn {
                                background-color: #424242;
                                border-bottom: 3px solid #2d2d2d;
                            }
                        }
                    }
                }
            }

            .col-md-3:first-child .small-card {
                .cost-block {
                    background-color: #424242;
                    border-color: #2d2d2d;
                }
                .purchase-btn {
                    background-color: #e4e4e4;
                    color: #424242;
                    border-color: #c1c1c1;
                }                
            }
            .col-md-3:nth-child(2) .small-card {
                .cost-block {
                    background-color: $pastelaquaColor;
                    border-color: #55857b;
                }
                .purchase-btn {
                    background-color: $pastelaquaColor;
                    border-color: #55857b;
                }                   
            }
            .col-md-3:nth-child(3) .small-card {
                .cost-block {
                    background-color: $pastelgrassColor;
                    border-color: #8e9865
                }  
                .purchase-btn {
                    background-color: $pastelgrassColor;
                    border-color: #8e9865;
                }   
            }
            .col-md-3:last-child .small-card {
                .cost-block {
                    background-color: #e4e4e4;
                    color: #74b0a4;
                    border-color: #a7a7a7;
                }    
                .purchase-btn {
                    background-color: #424242;
                    border-color: #383838;
                }   
            }

            .price-card{
                border-radius: 5px;
                font-size: 16px;
                margin:0 auto;

                &.large-card {
                    width: calc(100% - 20px);
                    height: 440px;
                    max-width: 263px;
                }
                &.small-card {
                    width: calc(100% - 20px);
                    max-width: 203px;
                    height: 440px;
                }


                &.dark-bg {
                    h4 {
                        color: #f4f4f4;
                    }
                    li {
                        color: #b6b6b6;
                    }
                }
                &.white-bg {
                    h4, li {
                        color: #5c5c5c;
                    }
                    span {
                        color: #969696;
                    }
                }
                .cost-block {
                    background-color: $aquaColor;
                    padding: 11px 0px 11px;
                    border-bottom: 3px solid #17987f;
                }

                ul {
                    padding: 22px 0px 0px;
                    li{
                        list-style-type: none;
                        padding-top: 15px;
                    }
                }
                h4 {
                    padding: 32px 0px 32px;
                    font-size: 20px;
                }

                .purchase-btn {
                    margin: 0 auto;
                    background-color: $grassColor;
                    margin-top: 42px;
                    border-bottom: 3px solid #798f21;
                }
            }
        }
    }
    .clients-opinion {
        text-align: center;
        padding-bottom: 63px;

        .box-header-gray {
            h3 {
                font-size: 18px;
            }
        }
        .quote-row {
            @include display(flex);
            @include justify-content(center);

            .quote-left,.quote-right {
                width: 100%;
                max-width: 76px;
                float: left;
            }
            .quote-left {
                background-image: url("../images/quote_icon.png");
                background-repeat: no-repeat;
                height: 100px;
                background-position: 90% 0%;
            }
            .quote-right {
                background-image: url("../images/quote_icon.png");
                background-repeat: no-repeat;
                height: 100px;
                transform: rotate(180deg);
                background-position: 90% 100%;

            }
            .quote {
                float:left;
                width: 470px;

                p:first-child {
                    padding: 0px 45px;
                    color: #717171;
                    line-height: 1.6;
                }
                .author {
                    color: $grassColor;
                    padding-top: 21px;
                }
            }
        }
    }
}

// OUR-TEAM
section.our-team {
    .browny-box {
        padding: 159px 0px 167px;
    }

    .about-us {
        text-align: center;

        .box-header-gray {
            padding: 54px 0px;
        }
        .person-row {
            @include display(flex);
            @include justify-content(space-between);
            @include flex-flow(wrap);
            max-width: 942px;
            margin: 0 auto;


        }
    }

    .our-skills {
        text-align: center;
        padding: 97px 0px 0px;

        .test-circle-row {
            width: 910px;
            max-width: 100%;
            overflow: hidden;
            margin: 0 auto;
            padding-bottom: 100px;
            .circle {

                &:last-child {
                    margin-right: 0px;
                }
            }
        }
    }
}

// PREFOOTER
section.prefooter {
    .browny-box {
        padding: 161px 0px 157px;

        .box-header {
            h2 {
                font-size: 50px;
                font-family: "Roboto-Regular";

                &:last-child {
                    font-family: "Roboto-Light";
                }
            }
        }
    }

    .contact-us {
        padding: 60px 0px 100px;
        text-align: center;

        .box-header-gray {
            padding-bottom: 70px;
        }
    }


    .contact-form {
        float: left;

        input {
            width: 49%;
            float: left;
            max-width: 326px;
            height: 34px;
            padding: 11px 0px 11px 14px;
            margin-bottom: 16px;
            font-size: 14px;
        }

        input[name=mail] {
            margin-left: 2%;
        }

        input[type=submit] {
            width: 104px;
            height: 30px;
            border-bottom: 2px solid #1f937d;
            float: left;
            background-color: #1bbc9d;
            color: #fff;
            padding: 0px;
            margin-bottom: 0px;
        }

        input,textarea {
            background-color: $input_bg;
            border: none;
        }
        textarea {
            width: 100%;
            max-width: 669px;
            height: 141px;
            padding-top: 11px;
            padding-left: 14px;
            margin-bottom: 12px;
        }
    }
    .contact-details {
        float: left;
        margin-left: 43px;

        p {
            padding-bottom: 15px;
            font-size: 16px;
            line-height: 30px;
        }
    }

    ul{
        padding-left: 0px;
        padding-top: 21px;

        li {
            @include display(inline-flex);
            @include justify-content(center);
            @include align-items(center);
            list-style-type: none;
            margin-right: 10px;
            width: 48px;
            height: 48px;


            &:last-child {
                margin-right: 0px;
            }

            &:hover {


                a {
                    width: 48px;
                    height: 48px;
                    background-color: $dark_bg;
                    border-radius: 50%;
                    color: #4b3f34;
                    text-decoration: none;
                }
                i {
                    width: 36px;
                    height: 36px;
                    background-color: $grassColor;
                    border-radius: 50%;
                    color: $dark_bg;
                }
            }
            a {
                width: 36px;
                height: 36px;
                background-color: $dark_bg;
                border-radius: 50%;
                @include display(flex);
                @include justify-content(center);
                @include align-items(center);
                color: #4b3f34;
            }
            i {
                @include display(flex);
                @include justify-content(center);
                @include align-items(center);
            }
        }
    }

    .recents-box {
        padding-top: 69px;
        padding-bottom: 64px;

        .recent-posts-box,.photo-stream-box {
            margin-left: 6.5%;
        }
        .recents-row {    

            .line-header-box {
                margin-bottom: 35px;
            }
            .recent-tweets-box {   
                i {
                    float: left;
                    color: $grassColor;
                }
                .info-tweet {
                    color: #747474;
                    padding-left: 38px;
                }
                p {
                    padding-top: 17px;
                    color: #747474;
                }
                .tweet-news {
                    padding-top: 26px;

                }
                .tweet-news:nth-of-type(1) {
                    padding-top: 0px;
                }
            }

            .recent-posts-box {
                .one-post-box {
                    padding-bottom: 16px;

                    .date-post-box,p {
                        color: #747474;
                    }
                    p {
                        padding-top: 17px;
                    }
                }
            }
            .photos-stream-row {
                padding-bottom: 9px;
                @include display(flex);
                @include justify-content(flex-start);

                img {
                    padding-left: 9px;

                    &:nth-of-type(1) {
                        padding-left: 0px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1025px) {
    .services-item-container,.photo-row {
        @include display(flex);
        @include justify-content(center);
    }
}
@media screen and ($desktop-xs) {
    .mar-left-xl {
        margin-left: 0px;
    }
    .one-four {
        min-height: 289px;

        &:nth-of-type(1) {
            padding-bottom: 20px;
        }
        &:nth-of-type(2) {
            padding-bottom: 20px;
        }
    }
    section.banner {
        background-image: none;
    }
    .circle {
        margin-right: initial;
    }
    .perc-num {
        display:block;
    }
}
@media screen and ($tablet) {
    section.our-services .services-item-container .one-four p {
        padding: 0px 10px 0px;
    }
    section.portfolio .portfolio-gray-box .photos-container .photo-row .photo-b {
        margin: 0px 10px;
    }
    section.portfolio .portfolio-gray-box .category-panel-list li:nth-of-type(4) {
        margin-top: 3px;
    }
    section.pricing .clients-opinion .quote-row .quote p:first-child {
        padding: 0px;
    }
    .quote-left,.quote-right {
        display: none;
    }
    .pricing-col-large,.pricing-col-small {
        padding-bottom: 20px;
    }
    section.pricing .our-price .pricing-row:first-child {
        padding-bottom: 40px;
    }
    section.prefooter .contact-form input,section.prefooter .contact-form textarea {
        display: block;
        margin: 0 auto 10px auto;
    }
    section.prefooter .contact-form textarea {
        max-width: 326px;
    }
    section.prefooter .contact-form input {
        width: 100%;
        float: none;
    }
    section.prefooter .contact-form input[name=mail] {
        margin-left:  auto;
        float: none;
    }
    section.prefooter .contact-form input[type=submit] {
        width: 100%;
        float: none;
    }
    section.prefooter .contact-form {
        margin-bottom: 60px;
        max-width: initial;
    }
    section.prefooter .contact-details {
        margin-left: initial;
        max-width: initial;
    }
    .line-header-box {
        text-align: center;

        &:after {
            width: 50%;
            margin: 0 auto;
        }
    }
    section.prefooter .recents-box .recent-posts-box, section.prefooter .recents-box .photo-stream-box {
        margin-left: 0px;
    }
    .recent-tweets-box,.recent-posts-box {
        padding-bottom: 60px;
    }
}


@media all and (max-width: 550px) {
    .person-row .person-details {
        width: 100%;
        padding-bottom: 50px;
    }
}

.progresss{
    position: relative;
    margin: 4px;
    float:left;
    text-align: center;
}
.barOverflow{ /* Wraps the rotating .bar */
    position: relative;
    //overflow: hidden; /* Comment this line to understand the trick */
    width: 145px; height: 45px; /* Half circle (overflow) */
    margin-bottom: -14px; /* bring the numbers up */

    &:after {
        content: '';
        position: relative;
        display: block;
        top: -75px;
        width: 145px;
        height: 90px;
        background-color: #282a2e;
    }
}
.bar{
    position: relative;
    top: 0; left: 0;
    width: 145px; height: 145px; /* full circle! */
    border-radius: 50%;
    box-sizing: border-box;
    border: 27px solid red;       /* half red, */
    border-bottom-color: green;  /* half green */
    border-right-color: green;
    
}