@import "variables";
@import "bourbon";

header {
    background-color: $dark_bg;
    height: 114px;
    font-family: 'Roboto-Regular';
    font-size: 16px;
    position: fixed;
    width: 100%;
    border-bottom: 1px solid #453a34;
    z-index: 100;

    .logo-block {
        display: inline-block;
        width: 100%;
        max-width: 369px;
    }
    .top-navigation {
        display: inline-block;

        ul {
            padding-left: 0px;

            li{
                display: inline-block;
                padding-left: 33px;

                &:first-child {
                    padding-left: 0px;
                }

                a {
                    color: $baseColor;

                    &:hover {
                        color: #799941;
                        text-decoration: none;
                    }
                }
            }
        } 
    }
    .search {
        display: inline-block;
        margin-left: 34px;
        width: 16px;
        height: 16px;
        background-image: url('../images/lupa.png');
        background-repeat: no-repeat;
    }
    .cbp-spmenu-vertical a {
        border-bottom: 1px solid #fff;
    }
    .cbp-spmenu {
        background: brown;

        h3 {
            background: #342923;
            color: #ccc;
        }
        ul {
            list-style-type: none;
        }
    }
}

@media screen and (max-width: 1199px) {
    header  .top-navigation,header .search {
        display: none;
    }
    header .logo-block,.hamburger {
        @include display(inline-flex);
    }
    .hamburger {
        position: absolute;
        top: 30px;
        right: 0;
    }
    .hamburger.push-body {
        left: initial;
    }
}